
@media only screen and (max-width: 320px) {
.viewLeft {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 120px;
            flex: 0 0 120px;
    width: 120px;
}
}
.rating-type {
  margin: 12px 18px 0 18px;
  padding-bottom: 18px;
  position: relative;
  font-size: 0;
}
.rating-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.rating-type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  font-size: 12px;
  line-height: 16px;
  color: #4d555d;
}
.rating-type .block .nums {
  font-size: 8px;
  margin-left: 4px;
}
.rating-type .block.active {
  color: #fff;
}
.rating-type .block.positive {
  background-color: rgba(0,160,220,0.2);
}
.rating-type .block.positive.active {
  background-color: #00a0dc;
}
.rating-type .block.negative {
  background-color: rgba(77,85,93,0.2);
}
.rating-type .block.negative.active {
  background-color: #4d555d;
}
.swith {
  padding: 12px 18px;
  line-height: 24px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #939995;
  font-size: 0;
}
.swith.on .icon-check_circle {
  color: #00c850;
}
.swith .icon-check_circle,
.swith .text {
  display: inline-block;
  vertical-align: top;
}
.swith .icon-check_circle {
  margin-right: 4px;
  font-size: 24px;
}
.swith .text {
  font-size: 12px;
}


