
@media only screen and (max-width: 320px) {
.viewLeft {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 120px;
            flex: 0 0 120px;
    width: 120px;
}
}
.foodDetailBox {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 46px;
  z-index: 20;
  width: 100%;
  background-color: #fff;
}
.foodDetailBox.move-enter-active,
.foodDetailBox.move-leave-active {
  transition: all 0.4s linear;
}
.foodDetailBox.move-enter-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.foodDetailBox.move-enter,
.foodDetailBox.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.foodDetailBox .imgBox {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.foodDetailBox .imgBox img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.foodDetailBox .imgBox .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.foodDetailBox .imgBox .back .icon-arrow_lift {
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.foodDetailBox .content {
  position: relative;
  padding: 18px;
}
.foodDetailBox .content .title {
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
  font-weight: 700;
}
.foodDetailBox .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.foodDetailBox .content .detail .sell-count,
.foodDetailBox .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.foodDetailBox .content .detail .sell-count {
  margin-right: 12px;
}
.foodDetailBox .content .price {
  font-weight: 700;
  line-height: 24px;
}
.foodDetailBox .content .price .now {
  margin-right: 12px;
  font-size: 14px;
  color: #f01414;
}
.foodDetailBox .content .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.foodDetailBox .content .cartcontrol-wrap {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.foodDetailBox .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  padding: 0 12px;
  box-sizing: border-box;
  border-radius: 12px;
  line-height: 24px;
  font-size: 10px;
  color: #fff;
  background-color: #00a0dc;
  transition: all 0.3s;
}
.foodDetailBox .content .buy.fade-enter-active {
  opacity: 1;
}
.foodDetailBox .content .buy.fade-enter,
.foodDetailBox .content .buy.fade-leave-active {
  opacity: 0;
}
.foodDetailBox .info {
  padding: 18px;
}
.foodDetailBox .info .title {
  margin-bottom: 6px;
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.foodDetailBox .info .text {
  padding: 0 8px;
  line-height: 24px;
  font-size: 12px;
  color: #4d555d;
}
.foodDetailBox .ratingSelect .title {
  padding: 18px 0 6px 18px;
  line-height: 18px;
  font-size: 14px;
}
.foodDetailBox .ratingSelect .rating-list {
  padding: 0 18px;
}
.foodDetailBox .ratingSelect .rating-list .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.foodDetailBox .ratingSelect .rating-list .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.foodDetailBox .ratingSelect .rating-list .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .text .icon-thumb_up,
.foodDetailBox .ratingSelect .rating-list .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .user .name {
  display: inline-block;
  margin-right: 6px;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
}
.foodDetailBox .ratingSelect .rating-list .rating-item .user .avater {
  border-radius: 50%;
}
.foodDetailBox .no-rating {
  padding: 16px 0;
  font-size: 12px;
  text-align: center;
  color: #93999f;
}


