
.cartControl {
  font-size: 0;
}
.cartControl .minus {
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.cartControl .minus .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartControl .minus.move-enter-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartControl .minus.move-enter,
.cartControl .minus.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(26px, 0, 0);
          transform: translate3d(26px, 0, 0);
}
.cartControl .minus.move-enter .inner,
.cartControl .minus.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartControl .count {
  display: inline-block;
  line-height: 24px;
  width: 12px;
  padding-top: 6px;
  vertical-align: top;
  font-size: 10px;
  text-align: center;
  color: #93999f;
}
.cartControl .add {
  display: inline-block;
  line-height: 24px;
  padding: 6px;
  font-size: 24px;
  color: #00a0dc;
}


